class FormValidator {
  requiredMessage = "Pole wymagane";
  minLengthMessage = "Zbyt maa liczba znakw";
  maxLengthMessage = "Zbyt dua liczba znakw";
  smallLetterMessage = "Wymagana co najmniej jedna maa litera";
  upperLetterMessage = "Wymagana co najmniej jedna dua litera";
  digitMessage = "Wymagana co najmniej jedna cyfra";
  emailMessage = "Niepoprawny e-mail";
  dateMessage = "Nieprawidowa data";
  onlyLettersMessage = "Wymagane tylko litery";

  checkIfIsRequired = (text: string) => {
    return text && !/^\s*$/.test(text);
  };

  checkMinLength = (text: string, minLength: number) => {
    return text.length >= minLength;
  };

  checkMaxLength = (text: string, maxLength: number) => {
    return text.length <= maxLength;
  };

  checkContainsSmallLetter = (text: string) => {
    return /[a-z]/.test(text);
  };

  checkContainsUpperLetter = (text: string) => {
    return /[A-Z]/.test(text);
  };

  checkContainsDigit = (text: string) => {
    return /[0-9]/.test(text);
  };

  checkEmail = (text: string) => {
    return /\S+@\S+\.\S+/.test(text);
  };

  checkDate = (text: string) => {
    return !isNaN(Date.parse(text));
  };

  checkOnlyLetters(text: string) {
    return /^[a-zA-Z]/.test(text);
  }
}

export default new FormValidator();